import './home.scss';
import Profile from './../../assets/Profile-main.jpeg';
import Typed from 'react-typed';
import { swiper } from '../sidebar/Sidebar';


const Home = () => {
  return (
    <section className='section home'id='home' >
        <div className="container">
            <div className="row">
                <div className="home-info padd-15">
                    <h3 className="hello">Hello, My name is <span className="name">Taha Bourichi</span></h3>
                    <h3 className="profession">I'm a <span className="typing"><Typed
                    strings={['Web Developer','Front-end Developer','Back-end Developer','Mern-stack Developer']}
                    typeSpeed={40}
                    backSpeed={50}
                    loop={true}
                    backDelay={1000}
                /></span></h3>
                    <p>
                        Lorem ipsum dolor sit amet consectetur adipisicing elit. 
                        Delectus suscipit tempore deleniti repudiandae maiores illo 
                        reprehenderit explicabo quod tempora sint ut numquam labore 
                        laboriosam quidem, earum, fugit consequuntur? Culpa, 
                        reprehenderit.
                    </p>
                    <a href="#contact" className="btn hire-me" onClick={()=>{swiper('contact')}}>Hire Me</a>
                </div>
                <div className="home-img padd-15">
                    <img src={Profile} alt="" />
                </div>
            </div>
        </div>
    </section>
  )
}

export default Home