import './sidebar.scss';
import HomeIcon from '@mui/icons-material/Home';
import AddCommentIcon from '@mui/icons-material/AddComment';
import PersonIcon from '@mui/icons-material/Person';
import BusinessCenterIcon from '@mui/icons-material/BusinessCenter';
import PlaylistAddCheckIcon from '@mui/icons-material/PlaylistAddCheck';




export function swiper(type) {
  const sections = document.querySelectorAll('section');
  const navs = document.querySelectorAll('ul.nav li a');
  const aside = document.querySelector('.aside');
  const navToggle = document.querySelector('.nav-toggle');
  navToggle.classList.remove('open');
  aside.classList.remove('open');
  sections.forEach(section => {  
    section.classList.remove('before');
    if (section.classList.contains('active')) {
      section.classList.add('before');
    }    
    section.classList.remove('active');
  });
  sections.forEach(section => {
    if (type === section.id) {
      section.classList.add('active');
    }
  });
  navs.forEach(nav => {
    nav.classList.remove('active');
    if (nav.classList.contains(type.split('')[0]) ) {
      nav.classList.add('active');
    }
  });
}






const Sidebar = () => {




  
  
  const navToggle = (e) => {
    const aside = document.querySelector('.aside');
    const navToggle = document.querySelector('.nav-toggle');
    aside.classList.toggle('open');
    navToggle.classList.toggle('open');
  }
  return (
    <>
        <div className="logo">
            <a href="#home" onClick={()=>{swiper('home')}} ><span>O</span>verlord</a>
        </div>
        {/* for mobile below */}
        <div className="nav-toggle" onClick={navToggle}>
            <span></span>
        </div>
        {/* for mobile up */}
        <ul className="nav">
            <li><a className="h active" href="#home" onClick={()=>{swiper('home')}}><HomeIcon className='icon'/> Home</a></li>
            <li><a className="a" href="#about" onClick={()=>{swiper('about')}}><PersonIcon className='icon'/> About</a></li>
            <li><a className="s" href="#services" onClick={()=>{swiper('services')}}><PlaylistAddCheckIcon className='icon'/>Services</a></li>
            <li><a className="p" href="#portfolio" onClick={()=>{swiper('portfolio')}}><BusinessCenterIcon className='icon'/>Portfolio</a></li>
            <li><a className="c" href="#contact" onClick={()=>{swiper('contact')}}><AddCommentIcon className='icon'/>Contact</a></li>
        </ul>
    </>
  )
}

export default Sidebar