import { Call,AddLocationAlt, Email, Web } from '@mui/icons-material';
import { useRef } from 'react';
import './contact.scss';
import emailjs from '@emailjs/browser';

const Contact = () => {
    const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_k8qdyrq', 'template_giu4ing', form.current, 'user_DLi4HUetQbj9btmASRcdz')
      .then((result) => {
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });
  };
    return (
        <section className="contact section " id='contact'>
            <div className="container">
                <div className="row">
                    <div className="section-title padd-15">
                        <h2>Contact </h2>
                    </div>
                </div>
                <h2 className="contact-title">SOMETHING TO INQUIRE ABOUT ?</h2>
                <h3 className="contact-sub-title">I'm at your service</h3>
                <div className="row">
                    <div className="contact-info-item padd-15">
                        <div className="icon"><Call className='real-icon'/></div>
                        <h4>Call Us On</h4>
                        <p>+212 626661516</p>
                    </div>
                    <div className="contact-info-item padd-15">
                        <div className="icon"><AddLocationAlt className='real-icon'/></div>
                        <h4>Office</h4>
                        <p>Tangier, Morocco</p>
                    </div>
                    <div className="contact-info-item padd-15">
                        <div className="icon"><Email className='real-icon'/></div>
                        <h4>Email</h4>
                        <p>Bourichi.taha@gmail.com</p>
                    </div>
                    <div className="contact-info-item padd-15">
                        <div className="icon"><Web className='real-icon'/></div>
                        <h4>Website</h4>
                        <p>www.taha.com</p>
                    </div>
                </div>
                <h2 className="contact-title" style={{marginTop:"60px"}}>GO AHEAD AND SEND ME AN EMAIL !</h2>
                <h3 className="contact-sub-title">I'm as responsive as my Websites</h3>
                <div className="row">
                    <form ref={form} onSubmit={sendEmail} className="contact-form padd-15">
                        <div className="row">
                            <div className="form-item col-6 padd-15">
                                <div className="form-group">
                                    <input placeholder='Name' type="text" name="user_name" id="" className="form-control" />
                                </div>
                            </div>
                            <div className="form-item col-6 padd-15">
                                <div className="form-group">
                                    <input placeholder='Email' type="email" name="user_email" id="" className="form-control" />
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="form-item col-12 padd-15">
                                <div className="form-group">
                                    <input placeholder='Subject' type="text" name="user_subject" id="" className="form-control" />
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="form-item col-12 padd-15">
                                <div className="form-group">
                                    <textarea name="message" id="" className="form-control" placeholder='Message ...'></textarea>
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="form-item col-12 padd-15">
                                <div className="buttons">
                                    <button type="submit" className="btn">Send Message</button>
                                </div>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </section>
    )
}

export default Contact