import { DarkMode, LightMode, Settings } from '@mui/icons-material';
import { ClickAwayListener } from '@mui/material';
import { useEffect, useState } from 'react';
import './switcher.scss';


function setActiveStyle(title) {
    const styles = document.querySelectorAll(".alternative-style");
    styles.forEach((style)=>{
        if (style.getAttribute("title") === title) {
            style.removeAttribute("disabled");
        }else{
            style.setAttribute('disabled',true)
        }
    })
}





const Switcher = () => {
    const [isSwitcher, setIsSwitcher] = useState(false);
    const [isDark, setIsDark] = useState(false);
    
    function clickHandler(e) {
        setIsDark(!isDark);
        const body = document.body;
        body.classList.toggle('dark');
    }

    useEffect(() => {
        function scroller() {
            setIsSwitcher(false);
        }
        document.addEventListener('scroll', scroller);
        return()=>{
            document.removeEventListener('scroll',scroller);
        }
    },[])
    return (
        <ClickAwayListener
            mouseEvent="onMouseDown"
            touchEvent="onTouchStart"
            onClickAway={()=>{setIsSwitcher(false)}}
        >
            
            <div className={isSwitcher ? "switcher open" : "switcher"}  >
                <div className={"switcher-toggler s-icon"} onClick={() => { setIsSwitcher(!isSwitcher) }}>
                    <Settings className='setting-icon' />
                </div>
                <div className="day-night s-icon" onClick={clickHandler}>
                    {isDark ? <DarkMode /> : <LightMode/>}
                </div>
                <h4>Theme Colors</h4>
                <div className="colors">
                    <span className="color-1" onClick={() => { setActiveStyle('color-1') }}></span>
                    <span className="color-2" onClick={() => { setActiveStyle('color-2') }}></span>
                    <span className="color-3" onClick={() => { setActiveStyle('color-3') }}></span>
                    <span className="color-4" onClick={() => { setActiveStyle('color-4') }}></span>
                    <span className="color-5" onClick={() => { setActiveStyle('color-5') }}></span>
                </div>
            </div>
        </ClickAwayListener>
    )
}

export default Switcher